<template>
  <div class="container pt-5 pb-5" style="max-width: 500px; text-align: left">
    <div class="alert alert-success" role="alert">
      <h2 class="alert-heading text-center">Đăng ký tài khoản</h2>
    </div>
    <!-- @submit.prevent="handleSubmit" -->
    <form @submit.prevent="handleSubmit" method="POST">
      <div class="form-group mt-4">
        <label class="mb-2" for="name">Họ tên:</label>
        <input
          type="text"
          v-model="userForm.name"
          id="name"
          name="name"
          class="form-control"
          :class="{ 'is-invalid': isSubmitted && $v.userForm.name.$error }"
        />
        <div
          v-if="isSubmitted && !$v.userForm.name.required"
          class="invalid-feedback"
        >
          Họ tên không được bỏ trống
        </div>
      </div>

      <div class="form-group mt-4">
        <label class="mb-2" for="email">Email:</label>
        <input
          type="email"
          v-model="userForm.email"
          id="email"
          name="email"
          class="form-control"
          :class="{ 'is-invalid': isSubmitted && $v.userForm.email.$error }"
        />
        <div
          v-if="isSubmitted && $v.userForm.email.$error"
          class="invalid-feedback"
        >
          <span v-if="!$v.userForm.email.required"
            >Email không được bỏ trống</span
          >
          <span v-if="!$v.userForm.email.email"
            >Vui lòng nhập đúng định dạng Email</span
          >
        </div>
      </div>

      <div class="form-group mt-4">
        <label class="mb-2" for="mobile">Số điện thoại:</label>
        <input
          type="text"
          v-model="userForm.mobile"
          id="mobile"
          name="mobile"
          class="form-control"
          :class="{ 'is-invalid': isSubmitted && $v.userForm.mobile.$error }"
        />
        <div
          v-if="isSubmitted && $v.userForm.mobile.$error"
          class="invalid-feedback"
        >
          <span v-if="!$v.userForm.mobile.required"
            >Số điển thoại không được bỏ trống</span
          >
        </div>
      </div>

      <div class="form-group mt-4">
        <label class="mb-2" for="address">Địa chỉ:</label>
        <input
          type="text"
          v-model="userForm.address"
          id="address"
          name="address"
          class="form-control"
          :class="{ 'is-invalid': isSubmitted && $v.userForm.address.$error }"
        />
        <div
          v-if="isSubmitted && !$v.userForm.address.required"
          class="invalid-feedback"
        >
          Địa chỉ không được bỏ trống
        </div>
      </div>

      <div class="form-group mt-4">
        <label class="mb-2" for="password">Mật khẩu:</label>
        <input
          type="password"
          v-model="userForm.password"
          id="password"
          name="password"
          class="form-control"
          :class="{
            'is-invalid': isSubmitted && $v.userForm.password.$error,
          }"
        />
        <div
          v-if="isSubmitted && $v.userForm.password.$error"
          class="invalid-feedback"
        >
          <span v-if="!$v.userForm.password.required"
            >Mật khẩu không được bỏ trống</span
          >
          <span v-if="!$v.userForm.password.minLength"
            >Mật khẩu ít nhất 8 ký tự</span
          >
        </div>
      </div>

      <div class="form-group mt-4">
        <label class="mb-2" for="confirmPassword">Nhập lại mật khẩu:</label>
        <input
          type="password"
          v-model="userForm.confirmPassword"
          id="confirmPassword"
          name="confirmPassword"
          class="form-control"
          :class="{
            'is-invalid': isSubmitted && $v.userForm.confirmPassword.$error,
          }"
        />
        <div
          v-if="isSubmitted && $v.userForm.confirmPassword.$error"
          class="invalid-feedback"
        >
          <span v-if="!$v.userForm.confirmPassword.required"
            >Không bỏ trống ô nhận lại mật khẩu</span
          >
          <span v-else-if="!$v.userForm.confirmPassword.sameAsPassword"
            >Mật khẩu không trùng khớp</span
          >
        </div>
      </div>
      <div class="form-group">
        <button
          type="submit"
          class="btn-register btn btn-info w-100 mt-4 btn-block"
        >
          Đăng ký
        </button>
      </div>
    </form>
  </div>
</template>
<script>
import { required, email, minLength, sameAs } from "vuelidate/lib/validators";
import { mapActions } from "vuex";
export default {
  name: "Register",
  data() {
    return {
      userForm: {
        name: "",
        email: "",
        mobile: "",
        address: "",
        password: "",
        confirmPassword: "",
      },
      isSubmitted: false,
    };
  },
  validations: {
    userForm: {
      name: {
        required,
      },
      email: {
        required,
        email,
      },
      mobile: {
        required,
      },
      address: {
        required,
      },
      password: {
        required,
        minLength: minLength(5),
      },
      confirmPassword: {
        required,
        sameAsPassword: sameAs("password"),
      },
    },
  },
  created: function () {},
  mounted(){},
  methods: {
    handleSubmit() {
      this.isSubmitted = true;
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.registerAccount()
    },
    ...mapActions(["registerAccount"]),
    // ...mapMutations(["registerAccount"]),
  },
};
</script>

<style>
.form-group > label {
  font-weight: 600;
}
</style>